.about_learnbox_right{
    text-align: left;
    display: table;
    width: 100%;
    margin-bottom: 30px;
    position: relative;
}

.about_learnbox_right:hover::after, .about_learnbox_left:hover:after{
    background-color: #ffffff;
    border: 3px solid #f6483b;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.about_learnbox_right:hover .about_learnbox_year h1, .about_learnbox_left:hover .about_learnbox_year h1{
    -webkit-animation-duration: 2s;
    -moz-animation-duration: 2s;
    -ms-animation-duration: 2s;
    -o-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-delay: 300ms;
    -moz-animation-delay: 300ms;
    -ms-animation-delay: 300ms;
    -o-animation-delay: 300ms;
    animation-delay: 300ms;
    animation-iteration-count: infinite;
    animation-name: pulse;
    color: #f6483b;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.about_learnbox_year{
    display: table-cell;
    width: 200px;
    vertical-align: middle;
}

.about_learnbox_year h1{
    font-size: 60px;
    margin: 0px;
    color: #202020;
    font-weight: 500;
}

.about_learnbox_info{
    display: table-cell;
    width: calc(100% - 200px);
}

.about_learnbox_info h4 {
    font-size: 18px;
    text-transform: capitalize;
    margin-top: 0px;
}

.about_learnbox_info span {
    display: inline-block;
    width: 100%;
    color: #ffffff;
    text-transform: capitalize;
    margin-bottom: 10px;
}

.about_learnbox_right:after{
    content: "";
    position: absolute;
    background-color: #000000;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    border: 3px solid #ffffff;
    top: 50%;
    left: -20px;
    margin-left: 2.5px;
    margin-top: -7px;
    z-index: 1;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.about_learnbox_left{
    display: table;
    width: 100%;
    margin-bottom: 30px;
    text-align: right;
    position: relative;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.about_learnbox_left .about_learnbox_year{
    float: right;
    padding-top:18px;
}

.about_learnbox_left .about_learnbox_info{
    float: left;
}

.about_learnbox_left::after{
    content: "";
    position: absolute;
    background-color: #000000;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    border: 3px solid #ffffff;
    top: 50%;
    right: -20px;
    margin-right: 3px;
    margin-top: -7px;
    z-index: 1;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}