


.about_heading{
    display: inline-block;
    position: relative;
}



.about_heading{
    margin-bottom: 30px;
}

.about_heading h1{
    font-size: 40px;
    margin: 0px;
    text-transform: uppercase;
    position: relative;
    width: auto;
    font-weight: 570;
    padding-bottom: 15px;
    color: #fff;
    space-between:1;
}

.about_heading h1::after{
    content: "";
    position: absolute;
    background-color: #ffffff;
    width: 100%;
    height: 2px;
    top: 100%;
    left: 0;
}

.about_heading p{
    text-align: right;
    width: auto;
    text-transform: uppercase;
    background-color: #010101;
    margin: 0px;
    padding-left: 10px;
    position: absolute;
    right: 0;
    bottom: -10px;
    color: #f6483b;
    font-weight: 500;
}

.about_btn{
    height: 50px;
    line-height: 50px;
    color: #ffffff;
    font-size: 15px;
    font-weight: 400;
    border-radius: 0px;
    padding: 0 20px;
    min-width: 140px;
    outline: none;
    cursor: pointer;
    display: inline-block;
    text-align: center;
    text-transform: capitalize;
    background-color: #f6483b;
    border: 1px solid #f6483b;
    text-decoration: none;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.about_btn:hover {
    background-color: #000;
    color: #f6483b;
    border: 1px solid #f6483b;
}