.top,.middle{
    padding-top:5rem;
}
.container{
    margin:auto;
    width:1146px;
}

.about_img{
    display: inline-block;
    width: 100%;
    text-align: center;
}

.about_img img{
    display: inline-block;
    width: 26rem;
}

.about_details{
    display: inline-block;
    width: 100%;
    color: white;
    margin-bottom: 10px;
}

.about_details p{
    letter-spacing: 1px;
    margin: 2rem 0;
}

.about_details a.about_btn{
    margin-right: 20px;
    margin-bottom: 10px;
}

.about_education{
    text-align: center;
    padding-bottom:3rem;
}

.about_learnsection{
    display: inline-block;
    width: 100%;
    position: relative;
    z-index: 1;
    margin-top: 2%;
}
.about_learnsection::after{
    content: "";
    position: absolute;
    background-color: #202020;
    width: 2px;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0px auto;
    /* z-index: -1; */
}

.about_heading_wrapper{
    text-align: left;
}

.footer, .footer .row, .footer_copyright p,.footer_icon,.footer_icon i{
    background-color: #0a0a0a;
}

.bottom{
    margin-top: 5rem;
    background-color: #0a0a0a;
}

.footer{
    margin: auto;
    padding:2rem;
    width: 1010px;
}


.footer_icon i{
    font-size: 35px;
    float: right;
    letter-spacing: 40px;
}

.contact_heading h1{
    font-size: 2rem;
    color: white;
    text-transform: uppercase;
    margin-bottom: 2.5rem;
}

.contact_form input,.contact_form textarea{
    width: 100%;
    border: none !important;
    background-color: #171515;
    height: 3rem;
    padding: 1rem;
    margin-bottom: 1.7rem;
}

.contact_form textarea{
    height:auto;
}

.portfolio_img img{
    margin-bottom: 0.5rem;
    width: 100%;
}